import React, { useEffect, useState } from 'react';
import Main from '../components/section/Main';
import { useSelector } from 'react-redux';
import { getCurrentTime, getDayType } from '../data/time';
import train from '../assets/img/nav/train.png';
import wait from '../assets/img/nav/waiting.png';
import walk from '../assets/img/nav/walk.png';

const Nav = () => {
    const startStation = useSelector(state => state.startStation);
    const endStation = useSelector(state => state.endStation);

    const [currentTime, setCurrentTime] = useState(getCurrentTime());
    const [dayType, setDayType] = useState(getDayType());

    const [cuTime, setCuTime] = useState("12:30:15");//현재시간 - 그냥 안움직이는거로
    const [fiTime, setfiTime] = useState("12:33:00");//탑승시간 - 종서
    const [path, setPath] = useState([]);//경로-리스트
    const [startLine, setStartLine] = useState("");//시작호선
    const [eachTime, seteachTime] = useState([]);//각호선별 탑승시간-리스트
    const [eachWalkingTime, seteachWalkingTime] = useState([]);//환승하는데 이동하는 시간-리스트
    const [eachTransferStation, seteachTransferStation] = useState([]);//환승하는 호선-리스트
    const [eachTypeOfLine, seteachTypeOfLine] = useState([]);//환승하는 호선-리스트
    const [eachWaitingTime, seteachWaitingTime] = useState([]);//환승하는데 기다리는 시간-리스트
    const [TotalTime, setTotalTime] = useState();//총 걸린시간
    const [finalTime, setfinalTime] = useState("");//예상도착시간 현재시간+총걸린시간


    useEffect(() => {
        const timer = setInterval(() => {
            setCurrentTime(getCurrentTime());
        }, 1000);

        return () => clearInterval(timer);
    }, []);

    const fetchDataFromSpring = async () => {
        try {
            const response = await fetch("http://backend:8080/SearchRoute", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({ "Start": startStation, "Finish": endStation }),
            });

            const data = await response.json();
            // 데이터 처리
            setCuTime(data.cuTime);
            setfiTime(data.fiTime);
            setPath(data.path);
            setStartLine(data.startLine);
            seteachTime(data.eachTime);
            seteachWalkingTime(data.eachWalkingTime);
            seteachTypeOfLine(data.eachTypeOfLine);
            seteachTransferStation(data.eachTransferStation);
            seteachWaitingTime(data.eachWaitingTime);
            setTotalTime(data.totalTime);
            setfinalTime(data.finalTime);

        } catch (error) {
            console.error('데이터 가져오기 실패:', error);
        }
    };

    useEffect(() => {
        if (startStation && endStation) {
            fetchDataFromSpring();
        }
    }, [startStation, endStation]);

    const getLineColor = (line) => {
        // 지하철 노선별 색상 매핑
        const lineColors = {
            '1호선': '#0d3692',
            '2호선': '#33a23d',
            '3호선': '#fe5d10',
            '4호선': '#00a2d1',
            '5호선': '#8b50a4',
            '6호선': '#c55c1d',
            '7호선': '#54640d',
            '8호선': '#f14c82',
        };
        return lineColors[line] || '#000'; // 기본 색상은 검정색
    };

    // 각 배열이 정의되어 있지 않으면 빈 배열로 설정하고 총 시간 계산
    const totaleachWalkingTime = (eachWalkingTime ?? []).reduce((acc, cur) => acc + cur, 0);
    const totaleachWaitingTime = (eachWaitingTime ?? []).reduce((acc, cur) => acc + cur, 0);
    const totaleachTime = (eachTime ?? []).reduce((acc, cur) => acc + cur, 0);

    // 총 시간 계산
    const totalTime = totaleachWalkingTime + totaleachWaitingTime + totaleachTime;

    // 비율 계산, 총 시간이 0일 경우 0으로 설정
    const ratio = totalTime > 0 ? 1000 / totalTime : 0;

    // 환승 횟수에 따라 막대 생성
    const renderTransferBars = () => {
        const transferBars = [];
        const iconSize = 20; // 이미지 크기

        for (let i = 0; i < eachTime.length - 1; i++) {
            const goBarWidth = eachTime[i] * ratio;
            const transferBarWidth = eachWalkingTime[i] * ratio;
            const waitingBarWidth = eachWaitingTime[i] * ratio;

            transferBars.push(
                <div key={`go${i}`} style={{ display: 'inline-block', width: `${goBarWidth}px`, height: '30px', backgroundColor: getLineColor(i === 0 ? startLine : eachTransferStation[i - 1]), position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                    <img src={train} alt="subway" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${goBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                    <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: getLineColor(i === 0 ? startLine : eachTransferStation[i - 1]) }}>{eachTime[i]}분-{(i === 0 ? startStation : eachTypeOfLine[i - 1])}({(i === 0 ? startLine : eachTransferStation[i - 1])})</span>
                </div>
            );
            transferBars.push(
                <div key={`transfer${i}`} style={{ display: 'inline-block', width: `${transferBarWidth}px`, height: '30px', backgroundColor: 'lightgray', position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                    <img src={walk} alt="walk" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${transferBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                    <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: '#000' }}>{eachWalkingTime[i]}분</span>
                </div>
            );
            transferBars.push(
                <div key={`wait${i}`} style={{ display: 'inline-block', width: `${waitingBarWidth}px`, height: '30px', backgroundColor: 'red', position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                    <img src={wait} alt="wait" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${waitingBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                    <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: '#000' }}>{eachWaitingTime[i]}분</span>
                </div>
            );
        }

        const lastGoBarWidth = eachTime[eachTime.length - 1] * ratio;

        transferBars.push(
            <div key={`go${eachTime.length - 1}`} style={{ display: 'inline-block', width: `${lastGoBarWidth}px`, height: '30px', backgroundColor: getLineColor(eachTransferStation[eachTransferStation.length - 1]), position: 'relative', whiteSpace: 'nowrap', overflow: 'visible' }}>
                <img src={train} alt="subway" style={{ position: 'absolute', left: '50%', top: '50%', transform: 'translate(-50%, -50%)', maxWidth: `${lastGoBarWidth}px`, maxHeight: '100%', width: 'auto', height: 'auto' }} />
                <span style={{ position: 'absolute', left: '50%', bottom: '-20px', transform: 'translate(-50%, 0)', color: getLineColor(eachTransferStation[eachTransferStation.length - 1]) }}>{eachTime[eachTime.length - 1]}분-{(eachTypeOfLine[eachTypeOfLine.length - 1])}({eachTransferStation[eachTransferStation.length - 1]})</span>
            </div>
        );

        return transferBars;
    };


    return (
        <Main title="실시간 길찾기" description="실시간 길찾기 페이지">
            <div className="resultsContainer">
                <h1 className="resultsHeader">출발지/도착지</h1>
                {startStation && <p className="resultItem">출발지: {startStation}</p>}
                {endStation && <p className="resultItem">도착지: {endStation}</p>}
                {!startStation && <p className="resultItem">출발지 정보가 없습니다.</p>}
                {!endStation && <p className="resultItem">도착지 정보가 없습니다.</p>}
            </div>
            <div className="resultsMap">
                <h1 className="resultsHeader">길찾기 결과</h1>
                <h3>출발 시간: {cuTime}, ({dayType})</h3>

                <div className="visualRepresentation" style={{ width: '1000px', height: '30px', backgroundColor: 'lightgray', margin: '20px 0' }}>
                    {renderTransferBars()}
                </div>
                <p className="scheduleTime">도착 시간(시간표): {finalTime}</p>
                {startLine && <p className="resultItem">탑승: {startStation}({startLine}) - {fiTime} 열차 탑승</p>}
                {eachTypeOfLine.map((name, index) => (
                    <p key={`transfer${index}`} className="resultItem">{index + 1}번 환승: {name}({eachTransferStation[index]}) - {(Number(fiTime) + Number(eachWaitingTime[index]) + Number(eachWalkingTime[index]) + Number(eachTime[index])).toString()} 열차 탑승</p>
                ))}
                {endStation && <p className="resultItem">하차: {endStation}({(eachTransferStation[eachTransferStation.length - 1])})</p>}
            </div>
        </Main>
    );
};

export default Nav;

{/*{eachTime && (
                    <>
                        <p className="resultItem">이동시간: {eachTime.join(', ')}</p>
                        <p className="resultItem">갈아타는 호선: {eachTransferStation.join(', ')}</p>
                        <p className="resultItem">시간표 반영 후 걸리는 시간: {TotalTime}</p>
                    </>
                )}
            <p className="resultItem">예상 최종 시간: {finalTime}</p>*/}